
.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  overflow: hidden;
  
}

.overlayVisible {
  visibility: visible;
  opacity: 1;

  .drawer {
  transform: translateX(0);
  }
}

.drawer {
  position: absolute;
  width: 420px;
  background: #FFFFFF;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  right: 0;
  height: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  transition: transform 0.3s cubic-bezier(0.63, 0.69, 0.47, -0.09);

  .items{
    flex: 1;
    overflow: auto;
    margin-bottom: 50px;
  }

  h2{
    margin: 0;
    font-size: 24px;

  }

}

